@import (reference) "widgets";
@import "ini";
@import "units";
@import "colorStyle";
#container{
  .absPosCenter();
  width: 1000px;height: 700px;
}
body{
  background-color: black;
}
@leftGalleryW:120px;
.dvContainer{

  .relative;
  width: 100%;
  height: 100%;
  border: 1px solid #424242;
  box-sizing: border-box;
  //padding-left: @leftGalleryW;
  .leftGallery{
    background-color: black;
    position: absolute;top:0;left: 0;z-index: @maskZ - 2000;
    height: 100%;
    width: @leftGalleryW;
    overflow-x: hidden;
    overflow-y: hidden;
    padding-top: 40px;
    .hideBtn{
      height: 40px;padding-top: 10px;position: absolute;top:0;left: 0;width: 100%;
      .js-hideGallery{
        height: 30px;display: block;
        font-size: 28px;
        line-height: 28px;
        text-align: center;
      }
      border-right: 8px solid #888888;
    }
    box-sizing: border-box;
    .gallery{
      overflow-x: hidden;
      overflow-y: scroll;
      &::-webkit-scrollbar {/*滚动条，这里不倒腾用户体验了，暂时在满足webkit，这里主要是想节约空间，其实大可不必通过滚动条*/
        width:8px;
        height:8px;
      }
      &::-webkit-scrollbar-thumb{
        background:#888888;
        border-radius:4px;
      }
      &::-webkit-scrollbar-track{//外层轨道
        background:#424242;width:8px;
      }
      &::-webkit-scrollbar-button    {
        background-color:#424242;height:4px;
      }
      height: 100%;
    }

    .imgBox{
      margin: 5px auto;
      border: 1px solid #8D9192;
      .relative;
      width:@leftGalleryW - 20px;
      height:@leftGalleryW - 20px;
      .img{height: 100%;width: 100%;}
      .tag{
        position: absolute;right: 10px;top: 10px;width: 50px;
      }
      .tag > .right{
        display: inline-block;float: right;
        font-size: 12px;
        text-align: right;color:#3B9DFF;
      }
      .tagL{
        position: absolute;left: 5px;top: 10px;width: 50px;
      }
      .tagL > .left{
        display: inline-block;float: left;
        font-size: 12px;
        text-align: left;color:#E6A300;
      }
      .imgIn{
        width:40px;
        height:40px;
        position: absolute;right: 2px;bottom: 2px;
        //background-color: lighten(#E0CC7A,20%);
        .opacity(0.5);
        span{
          color: darken(#E0CC7A,20%);;
          height: 40px;display: block;
          font-size:40px;
          line-height: 40px;
          text-align: center;
        }
      }
      .imgIn:active{
        background-color: lighten(#E0CC7A,20%);
      }
    }
    .imgBox.checked,.imgBox:hover{
      border: 1px solid #3B9DFF;
    }
  }
  .viewer{
    height:100%;
    width: 100%;overflow: hidden;
    box-sizing: border-box;position: relative;
    .viewport{
      width: 100%;height: 100%;
      .relative;
    }
    .seriesWindow{
      float: left;
      position: relative;border:1px solid #777777;box-sizing: border-box;overflow: hidden;
    }
    .seriesWindow.checked{
      border:1px solid #FFFF66;
    }
    .viewportWrapper{float: left;
      box-sizing: border-box;position: relative;border:1px solid #777777;
      .overlay{
        position: absolute;
        color: #e4ad00;
        font-size: 12px;
      }
    }
    .fullScreen{
      position: absolute;
      width: 40px;height: 40px;bottom: 30px;right:15px;z-index: @maskZ - 5000;
      span{
        display: block;.absPosCenter;
        width: 30px;height: 30px;color:white;line-height: 30px;text-align: center;
      }
    }
  }


}

//拖拽到图像中的遮罩
.dragToCanvDiv{
  position: absolute;
  width: @leftGalleryW - 20px;
  height: @leftGalleryW - 20px;
  border: 4px solid #424242;box-sizing: border-box;
  border-radius: 3px;
  background-color: black;z-index: @maskZ - 1000;
}
.viewer{
  .topPannel{
    //height:35px;
    width: 100%;.relative;.clear;
    .btnArea{
      .fa{color:#CECECE;}
      float: left;
      padding: 5px;display: inline-block;
      @btnSize:30px;
      .btn{
        width: @btnSize;
        height:@btnSize;float: left;
        background-color: transparent;
        position: relative;
        .icon{
          .absPosCenter;.x-img;
          height: @btnSize - 6px;
          width: @btnSize - 6px;
        }
      }
    }
    .btnArea.checked,.btnArea.leftBtnActive{
      .btn{
        background-color: #666666;
      }
    }
    .rightPaging{
      float: right;width: 126px;height: 40px;
      .prev,.next{
        display: inline-block;
        width: 40px;
        height: 40px;
        padding-top: 1px;padding-bottom: 1px;
        span{
          margin: auto;
          display: inline-block;width: 100%;
          height: 38px;line-height: 38px;text-align: center;
          font-size: 38px;color: #CCCCCC;
        }
      }
      .prev:active,.next:active{
        background-color: #666666;
      }
    }
  }
}
